* {
    box-sizing: border-box;
}
body {
    background-color: #A497AF;
}
.app-wrapper {
    margin: 0 auto;
    display: grid;
    width: 1200px;
    grid-template-areas: "h h" "n c";

    grid-template-rows: 80px 1fr;
    grid-template-columns: 2fr 10fr;
    /* grid-gap: 10px; */
}

.app-wrapper__content {
    grid-area: c;
    background-color: cornflowerblue;
}